import Rush from './Rush';
import React from 'react';
import Home from './Home';
import History from './History';
import People from './People';
import Activities from './Activities';
import Contact from './Contact';
import Undergraduate from './Undergraduate';
import Professor from './Professor';
import Alumni from './Alumni';
import Pledge from './Pledge';
import Objects from './Objects';
import Aedos from './Aedos';
import Ares from './Ares';
import Tree from './Tree';
import Princess from './princess';
import Selene from './selene';
import Royal from './royal';
import {
  HashRouter as Router,
  Routes,
  Route,
  Link
} from "react-router-dom";
import HttpsRedirect from 'react-https-redirect';

function App() {
  return (
    <HttpsRedirect>
      <Router>
          <div id="navigation">
            <Link to="/"></Link>
            <Link to="/rush"></Link>
            <Link to="/people"></Link>
            <Link to="/contact"></Link>
            <Link to="/history"></Link>
            <Link to="/activities"></Link>
            <Link to="/undergraduate"></Link>
            <Link to="/professor"></Link>
            <Link to="/alumni"></Link>
            <Link to="/pledge"></Link>
            <Link to="/objects"></Link>
            <Link to="/aedos"></Link>
            <Link to="/ares"></Link>
            <Link to="/tree"></Link>
            <Link to="/princess"></Link>
            <Link to="/selene"></Link>
            <Link to="/royal"></Link>
          </div>
          <div id="content">
            <Routes>
              <Route path="/" element={<Home />}/>
              <Route path="/rush" element={<Rush />}/>
              <Route path="/people" element={<People />}/>
              <Route path="/contact" element={<Contact />}/>
              <Route path="/history" element={<History />}/>
              <Route path="/activities" element={<Activities />}/>
              <Route path="/undergraduate" element={<Undergraduate />}/>
              <Route path="/professor" element={<Professor />}/>
              <Route path="/alumni" element={<Alumni />}/>
              <Route path="/pledge" element={<Pledge />}/>
              <Route path="/objects" element={<Objects />}/>
              <Route path="/aedos" element={<Aedos />}/>
              <Route path="/ares" element={<Ares />}/>
              <Route path="/tree" element={<Tree />}/>
              <Route path="/princess" element={<Princess />}/>
              <Route path="/selene" element={<Selene />}/>
              <Route path="/royal" element={<Royal />}/>
            </Routes>

          </div>
      </Router>
    </HttpsRedirect>
  )
}

export default App;
