import React, {useEffect, useState} from 'react';

export default function People()
{
    const [dropboxState, setDropboxState] = useState(false);
    const [dropbox, setDropbox] = useState(() => {
        return <div></div>;
    })

    useEffect(() => {
        console.log("USE EFFECT ", dropboxState)
        if (dropboxState)
        {
            setDropbox(<header>
                <nav>
                    <ul1>
                        <li><a href="/axe" class="hover-underline-animation">HOME</a></li>
                        <li><a href="/axe/#/rush" class="hover-underline-animation">RUSH</a></li>
                        <li><a href="/axe/#/people" class="hover-underline-animation">PEOPLE</a></li>
                        <li><a href="https://axe-beta-gamma.square.site/" class="hover-underline-animation">STORE</a></li>
                        <li><a href="https://axstutoring.github.io" class="hover-underline-animation">TUTORING</a></li>
                        <li><a href="/axe/#/contact" class="hover-underline-animation">CONTACT</a></li>
                    </ul1>
                </nav>
            </header>)
        }
        else
        {
            setDropbox(<div></div>);
        }


    }, [dropboxState])

    function toggleDropboxState()
    {
        setDropboxState(!dropboxState);
    }
    return (
        <>
            <button onClick={toggleDropboxState} class="toggle-button">
                <span class="bar"></span>
                <span class="bar"></span>
                <span class="bar"></span>
            </button>
            {dropbox}
            <header class="navOverlayImage">
                <nav>
                    <ul>
                        <li><a href="/axe" class="hover-underline-animation">HOME</a></li>
                        <li><a href="/axe/#/rush" class="hover-underline-animation">RUSH</a></li>
                        <li><a href="/axe/#/people" class="hover-underline-animation">PEOPLE</a></li>
                        <li><a href="https://axe-beta-gamma.square.site/" class="hover-underline-animation">STORE</a></li>
                        <li><a href="https://axstutoring.github.io" class="hover-underline-animation">TUTORING</a></li>
                        <li><a href="/axe/#/contact" class="hover-underline-animation">CONTACT</a></li>
                    </ul>
                </nav>
            </header>
            <img class="imageLoadDelay absolutePosition" id="AXSMembers" src="member.JPG" alt="AXS Members"/>
            <div class="imageTitleContainer" id="membersOfTheChemicalProfession">
                <h9>MEMBERS</h9>
                    <p9><br></br>of Beta Gamma Chapter</p9>
            </div>
            {/*<div className="scrollme" style={{"translate": "none", "rotate": "none", "scale": "none", "opacity": "0.9993", "visibility": "inherit", "transform": "translate3d(0px, -35.7072px, 0px)"}}> <svg width="30px" height="20px" aria-label="more content below"> <path stroke="#ffffff" stroke-width="2px" d="M2.000,5.000 L15.000,18.000 L28.000,5.000 "></path> </svg> </div>*/}
            <img class="imageLoadDelay absolutePosition" id="undergraduateMembers" src="undergrad.JPG" alt="Undergraduate Members"/>
            <h13 class="imageTitle" id="undergraduateMembersTitle">Undergraduate Members
                <div class="learnMorePosition" id="undergraduateMembersLearnMore">
                    <a href="/axe/#/undergraduate"><h6 class="learnMoreBox hover-highlight-animation">Learn More</h6></a>
                </div>
            </h13>
            
            <img class="imageLoadDelay absolutePosition" id="pledgeClass" src="pledge.JPG" alt="Pledge Class"/>
            <h13 class = "imageTitle" id="pledgeClassTitle">Pledge Classes
                <div class = "learnMorePosition" id="pledgeClassLearnMore">
                    <a href="/axe/#/pledge" onClick={window.scrollTo(0,0)}><h6 class="learnMoreBox hover-highlight-animation">Learn More</h6></a>
                </div>
            </h13>
            
            <img class="imageLoadDelay absolutePosition" id="professorMembers" src="professor.jpg" alt="Professor Members"/>
            <h13 class = "imageTitle" id="professorMembersTitle">Professor Members
                <div class="learnMorePosition" id="professorMembersLearnMore">
                    <a href="/axe/#/professor" onClick={window.scrollTo(0,0)}><h6 class="learnMoreBox hover-highlight-animation">Learn More</h6></a>
                </div>
            </h13>

            <img class="imageLoadDelay absolutePosition" id="alumniMembers" src="289458670_1037231557165581_8342686701647794390_n.jpg" alt="Alumni Members"/>
            <h13 class = "imageTitle" id="alumniMembersTitle">Alumni Members
                <div class ="learnMorePosition" id="alumniMembersLearnMore">
                    <a href="/axe/#/alumni" onClick={window.scrollTo(0,0)}><h6 class="learnMoreBox hover-highlight-animation">Learn More</h6></a>
                </div>
            </h13>
            <img class="imageLoadDelay absolutePosition" id="tree" src="tree1.jpg" alt="Tree"/>
            <h13 class = "imageTitle" id="treeTitle">Family Tree
                <div class ="learnMorePosition" id="treeLearnMore">
                    <a href="/axe/#/tree" onClick={window.scrollTo(0,0)}><h6 class="learnMoreBox hover-highlight-animation">Learn More</h6></a>
                </div>
            </h13>
            <div class="peopleContainer peopleTop">
                <div class="socialMediaGrid" id="peopleTop">
                    <div class="socialMedia1">
                    <h7>
                        <a href="https://www.facebook.com/uclaaxs/" class="hover-underline-animation">FACEBOOK</a>
                    </h7>
                    </div>
                    <div class="socialMedia2">
                        <h7>
                            <a href="https://www.instagram.com/uclaaxs/" class="hover-underline-animation">INSTAGRAM</a>
                        </h7>
                    </div>
                </div>
            </div>
        </>
    )
}